.page-wrapper {
    height: 100vh;
    width: 100vw;
    display: grid;
    grid-template-columns: 1fr 1fr;
    scrollbar-width: none;
    scroll-snap-type: x mandatory;
    scroll-snap-align: start;
    scroll-behavior: smooth;
    overflow-y: hidden;
    overflow-x: hidden;
}

.welcome {
    height: 100vh;
    width: 100vw;
    background: var(--off-white);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-family: 'Abril Fatface', cursive;
}

.welcome_img {
    width: auto;
    height: 35vh;
}

.welcome_text {
    margin-top: 5vh;
    width: 85vw;
    text-align: center;
    font-size: 2rem;
    letter-spacing: 0.2rem;
    margin-left: auto;
    margin-right: auto;
    overflow: hidden;
}

.welcome_text p {
    margin-top: 1rem;
    font-size: 1rem;
    letter-spacing: normal;
    font-family: 'Quicksand', sans-serif;
    line-height: 1.5rem;
    margin-bottom: 3vh;
}

.entry_point {
    height: 100vh;
    width: 100vw;
    font-family: 'Quicksand', sans-serif;
    display: grid;
    grid-template-columns: 1fr;
    scrollbar-width: none;
    scroll-snap-type: y mandatory;
    scroll-behavior: smooth;
    overflow-y: hidden;
    overflow-x: hidden;
}

.login {
    height: 100vh;
    width: 100vw;
    background: var(--white);
    background-image: url(/Todo-app/static/media/login.4bf9e97e.svg);
    background-size: 120vw;
    background-repeat: no-repeat;
    background-position: -10vw 60vh;
    display: flex;
    justify-content: center;
    font-weight: 700;
    overflow: hidden;
}

.login_content {
    margin-top: 10vh;
    display: flex;
    flex-direction: column;
    align-items: left;
    width: 80vw;
}

.login_content h1 {
    font-size: 2.5rem;
    margin-bottom: 5vh;
    color: var(--main);
}

.login_btn_container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}

.login_btn_container button {
    width: 35vw;
}

input {
    width: 80vw;
    height: 7vh;
    border-radius: 3rem;
    border: none;
    background: var(--light);
    font-size: 1.2rem;
    font-family: 'Quicksand', sans-serif;
    letter-spacing: 0.1rem;
    padding-left: 1rem;
    padding-right: 1rem;
    margin-bottom: 1rem;
    transition: var(--transition);
}

input:focus {
    border: 1px solid var(--main);
}

.forgot_password {
    font-size: 0.8rem;
    font-weight: 300;
    font-family: 'Quicksand', sans-serif;
    letter-spacing: 0.1rem;
    color: var(--black);
    text-align: right;
    line-height: 0rem;
}

.signup_alt {
    font-size: 0.8rem;
    font-weight: 300;
    font-family: 'Quicksand', sans-serif;
    letter-spacing: 0.1rem;
    color: var(--black);
    margin-top: 5vh;
    text-align: center;
}

.signup_alt span {
    color: var(--main);
    margin-left: 3vw;
    font-size: 1rem;
    font-weight: 500;
    cursor: pointer;
}

.signup {
    height: 100vh;
    width: 100vw;
    background: var(--white);
    background-image: url(/Todo-app/static/media/signup.a69ce7b3.svg);
    background-size: 80vw;
    background-repeat: no-repeat;
    background-position: center 83vh;
    display: flex;
    justify-content: center;
    font-weight: 700;
    overflow: hidden;
}

.signup_content {
    margin-top: 10vh;
    display: flex;
    flex-direction: column;
    align-items: left;
    width: 80vw;
}

.signup_content h1 {
    font-size: 2.5rem;
    margin-bottom: 5vh;
    color: var(--main);
}

.signup_btn_container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}

.signup_btn_container button {
    margin-top: vh;
    width: 35vw;
}

.error-modal {
    flex-direction: column;
    border: 2px solid var(--main);
    border-radius: 5vw;
    padding: 5vw;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    z-index: 100;
    position: absolute;
    background: var(--white);
    margin-top: 40%;
    transition: var(--transition);
    /* all these to hide the modal */
    height: -webkit-max-content;
    height: max-content;
    width: 90vw;
    display: none;
}

.show-error {
    display: flex;
}

.error-modal-close {
    font-size: 2rem;
    color: var(--main);
    display: flex;
    justify-content: right;
    width: auto;
}

.error-modal-main {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.error-msg {
    text-align: center;
    font-size: 0.9rem;
    font-weight: 700;
}

.error-img {
    width: 30vw;
    height: auto;
    margin-left: auto;
    margin-right: auto;
}

.password-tip {
    font-size: 0.65rem;
    color: var(--black);
    padding-left: 1rem;
    line-height: 1.3rem;
    font-weight: 500;
}

.password_container {
    position: relative;
    height: -webkit-max-content;
    height: max-content
}

.password_visibility {
    position: absolute;
    top: 42%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    font-size: 1.5rem;
    color: var(--black);
    cursor: pointer;
    right: 1rem;
    color: var(--grey);
    z-index: 2;
}

/* Small devices (landscape phones, 576px and up) */
/* @media (min-width: 576px) {} */

/* Medium devices (tablets, 768px and up) */
@media (min-width: 768px) {
    .welcome_text {
        width: 70vw;
    }

    input {
       height: 8vh;
       padding-left: 1.5rem;
    }

    .login_btn_container {
        width: 20vw;
    }

    .login {
        background-position: 40vw 60vh;
        background-size: 80vw;
    }

    .password-tip {
        line-height: 2rem;
        font-size: 0.8rem;
    }

    .signup_alt {
        font-size: 0.9rem;
        margin-top: 2vh;
    }

    .signup {
        background-size: 60vw;
        background-position: 30vw 82vh;
    }

    .error-modal {
        width: 50vw;
        margin-top: 20%;
    }

    .error-img {
        width: 20vw;
    }
    
}


/* Large devices (desktops, 992px and up) */
/* @media (min-width: 992px) {} */

/* X-Large devices (large desktops, 1200px and up) */
@media (min-width: 1200px) {
    .welcome_img {
        height: 40vh;
    }

    .welcome_text {
        font-size: 3rem;
    }

    .welcome_text p {
        font-size: 1rem;
    }

    input {
        width: 40vw;
    }

    .signup_alt {
        margin-top: 10vh;
        font-size: 1rem;
    }

    .password_visibility {
        -webkit-transform: translateX(-41vw);
                transform: translateX(-41vw);
        top: 20%;
    }

    .login {
        background-position: 39vw 3vh;
    }


    .signup {
        background-position: 40vw 20vh;
    }

    .error-modal {
        width: 50vw;
        margin-top: 10%;
    }

    .error-img {
        width: 15vw;
    }

    .error-msg {
        font-size: 1rem;
    }
}
body {
    background: var(--white);
    height: 99vh;
    overflow-y: hidden;
}

.body {
    margin: 1vw 6vw;
}

.header {
    margin-bottom: 7vw;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: var(--main);
    padding: 9vw 6vw;
    border-radius: 0 0 7vw 7vw;
    color: var(--white);
}

.logout {
    font-size: 1.3rem;
}

.add_task {
    display: flex;
    justify-content: space-between;
    transition: var(--transition);
}

.hide_task {
    height: 0;
    overflow: hidden;
    transition: var(--transition);
}

.add_task input {
    border-radius: 0.7rem;
    width: 71vw;
    height: 7vh;
    border: 1px solid var(--main);
    background: var(--white);
}

.main {
    height: 80vh;
    width: 100%;
    overflow-y: scroll;
    overflow-x: hidden;
}

.main::-webkit-scrollbar {
    display: none;
}

.add_task input:focus {
    /* you can put some other animation */
    background: var(--light);
}

.add_task button {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 15vw;
    height: 7vh;
    border-radius: 0.7rem;
    color: var(--white);
    background: var(--main);
    font-size: 1.2rem;
    font-weight: bold;
}

.no_tasks {
    display: flex;
    width: 100%;
    padding: 3vw;
    background: var(--main);
    color: var(--white);
    border-radius: 0.7rem;
    justify-content: center;
}

.tasks_pending {
    border-radius: 1rem;
    margin-bottom: 3vw;
    display: flex;
    align-items: center;
    min-height: 11vh;
    height: -webkit-max-content;
    height: max-content;
    transition: var(--transition);
}

.todo_content {
    display: flex;
    align-items: center;
    padding: 5vw;
    color: var(--white);
}

.task_text {
    font-size: 1rem;
    width: 50vw;
    word-wrap: break-word;
}

.tasks_pending:nth-child(7n+1) {
    background: rgb(255, 167, 52);
}

.tasks_pending:nth-child(7n+2) {
    background: rgb(169, 66, 247);
}

.tasks_pending:nth-child(7n+3) {
    background: rgb(12, 179, 76)
}

.tasks_pending:nth-child(7n+4) {
    background: rgb(255, 30, 187)
}

.tasks_pending:nth-child(7n+5) {
    background: rgb(149, 11, 23)
}

.tasks_pending:nth-child(7n+6) {
    background: rgb(16, 76, 119)
}

.tasks_pending:nth-child(7n+7) {
    background: rgb(233, 53, 170)
}


.tasks_completed {
    opacity: 0.5;
}

.tasks_pending .check {
    height: 5vw;
    width: 5vw;
    background: var(--white);
    border-radius: 2rem;
    margin-right: 5vw;
}

.tasks_completed .check {
    background: inherit;
    border: 2px solid white;
}

.alt_icons {
    width: 18vw;
    display: flex;
    justify-content: right;
    right: 0;
}

.alt_icons button {
    border: none;
    background: transparent;
    font-size: 1rem;
    padding-left: 1vw;
    font-size: 1.5rem;
    -webkit-transform: translateY(0.5vw);
            transform: translateY(0.5vw);
}


.edit_todo {
    color: var(--white);
    border-radius: 0.7rem;
    margin: 0;
    font-size: 1rem;
    height: inherit;
    padding: 0vw 4vw;
    display: flex;
    align-items: center;
    transition: var(--transition);
}

.edit_todo input {
    color: var(--black);
    border-radius: 0.7rem;
    border: none;
    margin: 0;
    font-size: 1rem;
    height: inherit;
    padding: 3.5vw 5vw;
    width: 61vw
}

.add_note {
    display: flex;
    align-items: center;
    justify-content: center;
}

.add_note button {
    color: var(--main);
    border-radius: 10vw;
    border: 1px solid var(--main);
    background: var(--white);
    font-size: 1.5rem;
    height: 15vw;
    width: 15vw;
    display: flex;
    justify-content: center;
    align-items: center;
    bottom: 0;
    margin-bottom: 1.5vw;
    position: fixed;
}

/* Small devices (landscape phones, 576px and up) */
/* @media (min-width: 576px) {} */

/* Medium devices (tablets, 768px and up) */
@media (min-width: 600px) {

    .header {
        margin-bottom: 3vw;
        padding: 6vw;
    }

    h2 {
        font-size: 2rem;
    }

    .logout {
        font-size: 2rem;
    }

    .add_task input {
        height: 8vh;
    }

    .add_task button {
        height: 8vh;
        font-size: 1.5rem;
    }

    .no_tasks {
        font-size: 1.3rem;
    }

    .main {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: repeat(6, 1fr);   
    }

    .tasks_pending {
        width: 42vw;
        min-height: 15vh;
        margin-left: auto;
        margin-right: auto;
    }

    .todo_content {
        padding: 3vw;
    }

    .task_text {
        width: 15vw;
        line-height: 1.5rem;
    }

    .tasks_pending .check {
        height: 3vw;
        width: 3vw;
        margin-right: 3vw;
    }

    .alt_icons {
        width: 15vw;
        margin-left: 0;
    }

    .alt_icons button {
        font-size: 1.5rem;
    }

    .edit_todo {
        padding: 0vw 2vw;
        width: 42vw;
    }

    .edit_todo input {
        height: 10vh;
        width: 35vw;
        padding: 2vw 2vw;
    }

    .add_note button {
        height: 7vw;
        width: 7vw;
        font-size: 1.5rem;
    }
}

/* Large devices (desktops, 992px and up) */
/* @media (min-width: 992px) {} */

/* X-Large devices (large desktops, 1200px and up) */
@media (min-width: 1200px) {

    .body {
        margin: 1vw 2vw;
    }

    .header {
        margin-bottom: 1vw;
        padding: 2vw 4.5vw;
        border-radius: 0;
        margin-bottom: 3vw;
    }

    h2 {
        font-size: 2rem;
    }

    .logout {
        font-size: 2rem;
    }

    .add_task {
        width: 50vw;
        margin-left: 1vw;
    }

    .add_task input {
        height: 10vh;
        width: 34vw;
        margin-bottom: 2vw;
    }

    .add_task button {
        height: 10vh;
        font-size: 2rem;
    }

    .main {
        grid-template-columns: repeat(4, 1fr);
        height: 75vh;
    }

    .tasks_pending {
        width: 23vw;
        margin-bottom: 1vw;
    }

    .todo_content {
        padding: 2vw;
    }

    .tasks_pending .check {
        height: 1.5vw;
        width: 1.5vw;
        margin-right: 1.4vw;
    }

    .alt_icons {
        width: 5.5vw;
    }

    .task_text {
        width: 11vw;
        line-height: 1.5rem;
    }

    .edit_todo {
       padding: 0vw 2vw;
       width: 16vw;
    }

    .edit_todo input {
        height: 10vh;
        width: 14.5vw;
        padding: 2vw 2vw;
    }

    .add_note {
        justify-content: right;
        margin-right: 2vw;
    }

    .add_note button {
        height: 3vw;
        width: 3vw;
    }
}
